<template>
	<span :style="{color: color, 'font-size': size + 'px' }" class="iconfont uni-icons" @click="_onClick">{{icons[type]}}</span>
</template>

<script>
	import icons from './icons.js';

	export default {
		name: 'icons',
		props: {
			type: {
				type: String,
				default: ''
			},
			color: {
				type: String,
				default: '#333333'
			},
			size: {
				type: [Number, String],
				default: 16
			}
		},
		data() {
			return {
				icons: icons
			}
		},
		methods: {
			_onClick() {
				this.$emit('click')
			}
		}
	}
</script>

